import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { LinkContainer } from 'react-router-bootstrap'
import { Table, Button } from 'react-bootstrap'
import Message from '../components/Message'
import Loader from '../components/Loader'

import { listUsers, deleteUser } from '../actions/userActions'

const UserListScreen = ({ history }) => {
    const dispatch = useDispatch()

    const userList = useSelector(s => s.userList)
    const { loading, error, users } = userList

    const userDelete = useSelector(s => s.userDelete)
    const { isSuccessful } = userDelete

    const loginUser = useSelector(s => s.loginUser)
    const { userInfo } = loginUser

    useEffect(() => {
        if (userInfo && userInfo.isAdmin) {

            dispatch(listUsers())
        } else {
            history.push('/login')
        }
    }, [dispatch, history, isSuccessful, userInfo])

    const handleDeleteUser = id => {
        if (window.confirm('Are you sure??')) {
            dispatch(deleteUser(id))
        }
    }

    return (
        <>
            {loading ? <Loader /> : error ? <Message variant='danger'>{error}</Message> : (
                <Table bordered hover striped responsive className='table-sm'>
                    <thead>
                        <tr>
                            <th>Id</th>
                            <th>Name</th>
                            <th>Email</th>
                            <th>Admin</th>
                            <th>Actions</th>
                        </tr>
                    </thead>

                    <tbody>
                        {users && users.map(user => (
                            <tr key={user._id}>
                                <td>{user._id}</td>
                                <td>{user.name}</td>
                                <td>{user.email}</td>
                                <td>{user.isAdmin ? <i className='fas fa-check' style={{ color: 'green' }}></i> : (
                                    <i className='fas fa-times' style={{ color: 'red' }}></i>
                                )}</td>
                                <td>
                                    <LinkContainer to={`user/${user._id}/edit`}>
                                        <Button variant='light' className='btn-sm'><i className='fas fa-edit' style={{ color: 'blue' }}></i></Button>
                                    </LinkContainer>
                                    <Button variant='damger' type='button' className='btn-sm' onClick={() => handleDeleteUser(user._id)}><i className='fas fa-trash' style={{ color: 'red' }}></i></Button>

                                </td>
                            </tr>
                        ))}
                    </tbody>
                </Table>
            )}
        </>
    )
}

export default UserListScreen
