import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { LinkContainer } from 'react-router-bootstrap'
import { Table, Button } from 'react-bootstrap'
import Message from '../components/Message'
import Loader from '../components/Loader'

import { listOrders } from '../actions/orderActions'


const OrderListScreen = ({ history }) => {
    const dispatch = useDispatch()

    const orderList = useSelector(s => s.orderList)
    const { loading, error, orders } = orderList


    const loginUser = useSelector(s => s.loginUser)
    const { userInfo } = loginUser

    useEffect(() => {

        if (userInfo && userInfo.isAdmin) {
            dispatch(listOrders())
        } else {
            history.push('/login')
        }
    }, [dispatch, history, userInfo])


    return (
        <>
            <h1>Orders</h1>

            {loading ? <Loader /> : error ? <Message variant='danger'>{error}</Message> : (
                <Table bordered hover striped responsive className='table-sm'>
                    <thead>
                        <tr>
                            <th>orderId</th>
                            <th>UserId</th>
                            <th>Date</th>
                            <th>Total Price</th>
                            <th>isPaid</th>
                            <th>IsDelivered</th>
                            <th>Action</th>
                        </tr>
                    </thead>

                    <tbody>
                        {orders && orders.map(order => (
                            <tr key={order._id}>
                                <td>{order._id}</td>
                                <td>{order.user && order.user.name}</td>
                                <td>{order.createdAt.substring(0, 10)}</td>
                                <td>${order.totalPrice}</td>
                                <td>{order.isPaid ? order.paidAt.substring(0, 10) : <i className='fas fa-times' style={{ color: 'red' }}></i>}</td>
                                <td>{order.isDelivered ? order.updatedAt.substring(0, 10) : <i className='fas fa-times' style={{ color: 'red' }}></i>}</td>
                                <td>
                                    <LinkContainer to={`/order/${order._id}`}>
                                        <Button variant='light' className='btn-sm'>Details</Button>
                                    </LinkContainer>

                                </td>
                            </tr>
                        ))}
                    </tbody>
                </Table>
            )}
        </>
    )
}

export default OrderListScreen
